import router from './router'
import store from './store'
import NProgress from 'nprogress' // Progress 
import 'nprogress/nprogress.css' // Progress 
import {Message} from 'element-ui'
import {getToken} from '@/utils/auth' // 

const whiteList = ['/login', '/'] // 
router.beforeEach((to, from, next) => {
  NProgress.start()
  if (getToken()) {
    if (to.path === '/login') {
      next({path: '/'})
      NProgress.done() // if current page is dashboard will not trigger	afterEach hook, so manually handle it
      return
    }
    if (store.getters.name === null || store.getters.name === '' || store.getters.name.length === 0) { // user_info
      store.dispatch('GetInfo').then(res => { // 
        const roles = res.content.roles // note: roles must be a array! such as: ['editor','develop']
        store.dispatch('GenerateRoutes', {roles}).then(() => { // roles
          router.addRoutes(store.getters.addRouters) // 
          next({...to, replace: true}) // hack addRoutes ,set the replace: true so the navigation will not leave a history record
        })
        return
      }).catch((err) => {
        store.dispatch('FedLogOut').then(() => {
          Message.error(err || 'Verification failed, please login again')
          next({path: '/'})
        })
      })
    }
    next()
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      next('/login')
      NProgress.done()
    }
  }
})

router.afterEach(() => {
  NProgress.done() // Progress/
})



